export default {
  name: "forgot",
  created() {
    this.init();
  },
  data() {
    return {
      encoded_string: undefined,
      captcha_key: undefined,
      user: {},
      submit_loading: false,
    }
  },
  methods: {
    on_submit: function () {
      this.submit_loading = true;
      var post_data = this.user;
      post_data.key = this.captcha_key;

      console.log('captcha_key:', this.captcha_key);
      this.$axios.post('/app/user/forgot', post_data).then((response) => {
        this.$message('已发送邮件，请前往邮箱查看。', {
          theme: "error",
          position: "top-left",
          duration: 5000
        });
      }).catch((error) => {
        console.log('error:', error);
        this.submit_loading = false;

      });
      return false;
    },
    init: function () {
      this.$axios.get('/app/user/forgot').then((response) => {
        this.encoded_string = response.data.encoded_string;
        this.captcha_key = response.data.key;
      }).catch((error) => {

      });
    }
  }
}
